<template>
	<div id="my-comment">
		<public-header title="我的评价"></public-header>
		<div class="content font-14">
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
				<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
					class="comment-list"
				>
					<div class="comment-header">
						<div class="avatar">
							<img :src="member.avatar" class="img-100" alt="">
						</div>
						<div class="nickname">{{member.nickname}}</div>
						<div class="total" v-if="total > 0">已贡献{{total}}条评论</div>
					</div>
					<template v-for="(item, index) in records.data">
						<div class="comment-item">
							<router-link tag="div" :to="util.getUrl({path: '/pages/store/goods', query: {sid: item.sid}})" class="store flex-lr van-hairline--bottom">
								<div class="flex">
									<div class="logo">
										<img :src="item.logo" class="img-100" alt="">
									</div>
									<div class="title">{{item.title}}</div>
								</div>
								<div class="icon icon-right font-12 c-gray"></div>
							</router-link>
							<div class="inner">
								<div class="avatar border-1px">
									<img :src="member.avatar" class="img-100" alt="">
								</div>
								<div class="info">
									<div class="flex-lr">
										<div class="font-14">{{member.nickname}}</div>
										<div class="c-gray">{{item.addtime_cn}}</div>
									</div>
									<div class="flex margin-5-t">
										<div class="flex margin-15-r">
											<span>商品</span>
											<van-rate v-model="item.goods_quality" disabled-color="#ff2d4b" :size="12" disabled />
										</div>
										<div class="flex">
											<span>配送</span>
											<van-rate v-model="item.delivery_service" disabled-color="#ff2d4b" :size="12" disabled />
										</div>
									</div>
									<div class="note" v-if="item.note">
										{{item.note}}
									</div>
									<template v-if="item.thumbs && item.thumbs.length > 0">
										<div class="thumbs">
											<template v-for="(thumb, thumbIndex) in item.thumbs">
												<div class='thumb-item' @click="util.jsPreviewImage(item.thumbs, thumbIndex)">
													<img :src="thumb" class="img-100" alt="">
												</div>
											</template>
										</div>
									</template>
									<div class="good c-gray" v-if="item.good_cn">
										<div class="icon icon-appreciate margin-5-r"></div>
										<div>{{item.good_cn}}</div>
									</div>
									<div class="tags c-gray" v-if="item.deliveryer_tag">
										<div class="icon icon-tag margin-5-r"></div>
										<div>{{item.deliveryer_tag}}</div>
									</div>
									<template v-if="item.replytime > 0">
										<div class="reply">
											<div class="flex-lr margin-10-b">
												<div>商家回复</div>
												<div>{{item.replytime_cn}}</div>
											</div>
											<div class="reply-note">
												{{item.reply}}
											</div>
										</div>
									</template>
								</div>
							</div>
							<div class="btns van-hairline--top" v-if="item.can_update == 1">
								<router-link tag="div" :to="util.getUrl({path: '/pages/order/comment', query: {id: item.oid}})" class="btn-item">
									<div class="icon icon-comment"></div>
									<div>修改评价</div>
								</router-link>
							</div>
						</div>
					</template>
					<div class="loaded" v-if="records.finished">
						<div class="loaded-tips">没有更多了</div>
					</div>
				</van-list>
				<div class="no-data" v-else>
					<img src="static/img/comment_no_con.png" alt="">
					<p>暂无评论~</p>
				</div>
			</van-pull-refresh>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicFooter from '@/components/footer'
	import PublicHeader from '@/components/header'
	import {getRecords, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records: {
					page: 1,
					psize: 15,
					loading: true,
					finished: false,
					empty: false,
					data:[]
				},
				member: {
					nickname: '',
					avatar: '',
				},
				total: 0,
				isRefresh: false,
				showPreLoading: true
			}
		},
		methods: {
			onLoad(force) {
				var that = this;
				getRecords({
					vue: that,
					force: force,
					url: 'wmall/member/comment/list',
					data: {},
					success: (res) => {
						that.member = that.util.extend(that.member, res.member);
						that.total = res.total;
					}
				})
			},

			onPullDownRefresh(){
				this.onLoad(true);
			}
		},
		mounted() {
			this.onLoad();
		},
		components: {
			PublicHeader,
			PublicFooter
		},
	}
</script>

<style>
	#my-comment .comment-header{
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: center;
		padding: 20px 0;
		background-color: #FF2D4B;
		color: #fff;
	}
	#my-comment .comment-header .avatar{
		width: 50px;
		height: 50px;
		border-radius: 50%;
		overflow: hidden;
		font-size: 0px;
		margin-bottom: 15px;
	}
	#my-comment .comment-header .total{
		font-size: 12px;
		margin-top: 7px;
	}
	#my-comment .comment-item{
		background-color: #fff;
		margin-bottom: 3vw;
		padding-left: 3vw;
	}
	#my-comment .comment-item:last-child{
		margin-bottom: 0px;
	}
	#my-comment .comment-item .store{
		padding: 3vw 3vw 3vw 0;
	}
	#my-comment .comment-item .store .logo{
		width: 20px;
		height: 20px;
		border-radius: 3px;
		overflow: hidden;
		margin-right: 7px;
		font-size: 0px;
	}
	#my-comment .comment-item .inner{
		display: flex;
		flex-flow: row nowrap;
		align-items: flex-start;
		justify-content: space-between;
		padding-top: 3vw;
	}
	#my-comment .comment-item .avatar{
		width: 10vw;
		height: 10vw;
		border-radius: 50%;
		overflow: hidden;
		margin-right: 3vw;
		font-size: 0px;
	}
	#my-comment .comment-item .inner .info{
		width: 84vw;
		padding: 0 3vw 3vw 0;
		font-size: 12px;
	}
	#my-comment .comment-item .inner .info .note{
		font-size: 14px;
		line-height: 1.2;
		margin-top: 2vw;
	}
	#my-comment .comment-item .inner .info .thumbs{
		display: flex;
		flex-flow: row wrap;
		margin: 1vw 0 0 -1vw;
	}
	#my-comment .comment-item .inner .info .thumb-item{
		width: 20.25vw;
		height: 20.25vw;
		padding: 1vw;
		border-radius: 5px;
		overflow: hidden;
	}
	#my-comment .comment-item .inner .info .thumb-item img{
		border-radius: 5px;
	}
	#my-comment .comment-item .inner .info .good, #my-comment .comment-item .inner .info .tags{
		display: flex;
		align-items: flex-start;
		margin-top: 1vw;
		line-height: 1.2;
	}
	#my-comment .comment-item .inner .info .reply{
		background-color: #F4F4F4;
		color: #888;
		padding: 2.5vw;
		margin-top: 1vw;
		line-height: 1.2;
		border-radius: 3px;
	}
	#my-comment .comment-item .btns{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: flex-end;
		padding: 3vw 3vw;
	}
	#my-comment .comment-item .btns .btn-item{
		display: flex;
		align-items: center;
		margin-left: 3vw;
	}
	#my-comment .comment-item .btns .btn-item .icon{
		font-size: 16px;
		margin-right: 3px;
	}
</style>



